.kanban * {
  font-family: Raleway;
}

body {
  background-image: url("5160549.c39be43f.jpg");
  background-size: cover;
}

.task-buttons {
  flex-wrap: nowrap;
  justify-content: space-evenly;
  align-items: baseline;
  display: flex;
}

.header {
  color: #fff;
  text-transform: uppercase;
  flex-flow: row;
  justify-content: center;
  font-size: 80px;
  font-weight: 800;
  display: flex;
}

.kanban-lanes {
  border: 1px #000;
  border-radius: 10px;
  padding: 30px;
  display: flex;
}

.kanban-column {
  background-color: rgba(243, 238, 238, .263);
  border: 1px;
  border-radius: 5px;
  flex: 1;
}

.kanban-column:not(:last-child) {
  margin-right: 30px;
}

.column-title {
  color: #fff;
  -ms-user-select: none;
  user-select: none;
  flex-flow: row;
  justify-content: center;
  margin-bottom: 20px;
  font-size: 30px;
  font-weight: 800;
  display: flex;
}

.item-input {
  box-sizing: border-box;
  cursor: pointer;
  background: #fff;
  border-radius: 5px;
  padding: 10px 15px;
}

.createItemBtn {
  width: 100%;
  color: #1052d6;
  background-color: rgba(0, 0, 0, .1);
  border: 1px #000;
  border-radius: 5px;
  margin-top: 20px;
  padding: 10px 0;
  font-size: 16px;
}

.createItemBtn:hover, .createItemBtn:focus {
  cursor: pointer;
  color: #fff;
  background-color: rgba(16, 82, 214, .62);
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1) .1s;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.backdrop {
  width: 100%;
  height: 100%;
  background-color: rgba(46, 47, 66, .4);
  transition: visibility .25s linear, all .1s;
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal-content {
  width: 80%;
  background-color: #fefefe;
  border: 1px solid #888;
  margin: auto;
  padding: 20px;
}

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1) .1s;
}

.close:hover, .close:focus {
  color: #000;
  cursor: pointer;
  text-decoration: none;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1) .1s;
}

.modal-title {
  text-align: center;
  letter-spacing: .02em;
  color: rgba(16, 82, 214, .62);
  margin-bottom: 16px;
  font-family: inherit;
  font-size: 24px;
  font-weight: 600;
  line-height: 1.5;
}

.label {
  flex-direction: column;
  display: flex;
}

.form-label {
  letter-spacing: .04em;
  color: #217d7d;
  margin-bottom: 4px;
  font-family: Raleway;
  font-size: 16px;
  font-style: normal;
  font-weight: 200;
  line-height: 1.16667;
  position: relative;
}

.form-input {
  width: 100%;
  height: 40px;
  border: 1px solid #064545;
  border-radius: 4px;
  padding-left: 36px;
  font-family: inherit;
}

.form-input:focus {
  border: 1px solid #55e6e6;
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1) .1s;
}

input {
  outline: rgba(0, 0, 0, 0);
  padding: 8px;
  font-family: inherit;
}

.form-button {
  width: 169px;
  height: 56px;
  color: #217d7d;
  letter-spacing: .04em;
  background-color: rgba(0, 0, 0, .1);
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  margin-left: auto;
  margin-right: auto;
  padding: 16px 32px;
  font-size: 16px;
  display: flex;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.form-button:is(:hover, :focus) {
  cursor: pointer;
  color: #fff;
  background-color: #217d7d;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1) .1s;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.form-label span {
  margin-bottom: 4px;
  display: block;
}

.form-field {
  position: relative;
}

.form-input:focus + .form-icon {
  fill: var(--pressed-state);
  transition: fill .25s cubic-bezier(.4, 0, .2, 1) .1s;
}

.form-group {
  justify-content: center;
  margin-bottom: 8px;
  display: flex;
}

.text-input {
  width: 100%;
