  margin-bottom: 16px;
  padding: 8px 16px;
  display: block;
}

.text-input:focus {
  border: solid 1px 1px #55e6e6;
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1) .1s;
}

.task-name {
  text-transform: capitalize;
  flex-direction: row;
  justify-content: center;
  font-weight: 800;
  display: flex;
}

.task-description {
  font-weight: 400;
}

.task-description:first-letter, .option {
  text-transform: uppercase;
}

.task-card {
  background: rgba(0, 0, 0, .1);
  border: 1px solid #217d7d;
  border-radius: 5px;
  margin-top: 16px;
  padding: 5px;
}

.task-remove:is(:hover, :focus) {
  cursor: pointer;
  fill: #fff;
